import {useState} from 'react';

const useTags = () => {
    const TagsMap = [
        {id: 0, icon: 'repast', name: ''},
        {id: 1, icon: 'fruits', name: ''},
        {id: 2, icon: 'costume', name: ''},
        {id: 3, icon: 'housing', name: ''},
        {id: 4, icon: 'traffic', name: ''},
        {id: 5, icon: 'medicine', name: ''},
        {id: 6, icon: 'study', name: ''},
        {id: 7, icon: 'game', name: ''},
        {id: 8, icon: 'groceries', name: ''},
        {id: 9, icon: 'shopping', name: ''},
        {id: 10, icon: 'tobaccoandwine', name: ''},
        {id: 11, icon: 'phone', name: ''},
        {id: 12, icon: 'exercise', name: ''},
        {id: 13, icon: 'social', name: ''},
        {id: 14, icon: 'journey', name: ''},
        {id: 15, icon: 'loan', name: ''},
        {id: 16, icon: 'other', name: ''},
        {id: 100, icon: 'salary', name: ''},
        {id: 101, icon: 'parttime', name: ''},
        {id: 102, icon: 'investmentandfinance', name: ''},
        {id: 103, icon: 'bonussubsidy', name: ''},
        {id: 104, icon: 'borrow', name: ''},
        {id: 105, icon: 'reimbursement', name: ''},
        {id: 106, icon: 'social', name: ''},
        {id: 107, icon: 'other', name: ''},
    ];
    const [tagsList] = useState<{ id: number; icon: string; name: string }[]>(TagsMap);
    const getName = (id: number) => {
        const tag = TagsMap.filter(t => t.id === id)[0];
        return tag ? tag.name : '';
    };
    const getIconName = (id: number) => {
        const tag = TagsMap.filter(t => t.id === id)[0];
        return tag ? tag.icon : '';
    };
    return {tagsList, getName, getIconName};
};
export {useTags};